import React from 'react'
import Lottie from 'react-lottie';
import * as animationData from '../empty_status.json'

export default class LoadingGraphic extends React.Component {

  constructor(props) {
    super(props);
    this.state = {isStopped: false, isPaused: false};
  }

  render() {

    const defaultOptions = {
      loop: true,
      autoplay: true,
      animationData: animationData,
    };

    return (<div className="loading_graphic_container">

      <Lottie options={defaultOptions}
        height={400}
        width={400}
        isStopped={this.state.isStopped}
        isPaused={this.state.isPaused}/>
        <h1 className="loading_title">Coming Soon</h1>

      </div>);
    }
  }



